<!--  -->
<template>
  <div>
    <ul class="tag_en">
      <li :class="{active:prefix===''}" @click="prefix=''">热门</li>
      <template v-for="i in 26">
        <!-- i:1~26; 如果是(i,index) in 26，index:0~25 -->
        <li :key="i" :class="{active:prefix===String.fromCharCode(64+i)}" @click="prefix=String.fromCharCode(64+i)">{{String.fromCharCode(64+i)}}</li>
      </template>
      <li :class="{active:prefix==='%23'}" @click="prefix='%23'">#</li>
    </ul>
    <ul class="tag_kind">
      <template v-for="(item,index) in singerTags">
        <li :key="index" :class="{active:singerTagId===item.id}" @click="singerTagId=item.id">{{item.name}}</li>
      </template>
    </ul>
    <div class="artist_con">
      <singer-list :artistData="artistData"></singer-list>
    </div>
  </div>
</template>

<script>
import { HttpManager } from "../api";
import SingerList from '../components/SingerList.vue';
export default {
  name: "Singers",
  data () {
    return {
      singerTags: [{ id: 0, name: "全部" }, { id: 1, name: "华语男" },
      { id: 2, name: "华语女" }, { id: 3, name: "华语组合" }, { id: 4, name: "日韩男" },
      { id: 5, name: "日韩女" }, { id: 6, name: "日韩组合" }, { id: 7, name: "欧美男" },
      { id: 8, name: "欧美女" }, { id: 9, name: "欧美组合" }, { id: 10, name: "其他" },],
      // code: 0,
      singerTagId: 0,
      // artistList: [],
      artistData: {},
      // isPlay: false,
      // total: 0,
      prefix: ""
    };
  },

  components: {
    SingerList
  },

  methods: {
    getSingerList (val) {
      var that = this;
      HttpManager.getSingerList({
        category: this.singerTagId,
        prefix: this.prefix,
        pn: val,
        rn: 60
      })
        .then((res) => {
          // 代替 `Object.assign(this.someObject, { a: 1, b: 2 })`,这种方法不能响应更新
          that.artistData = Object.assign({}, that.artistData,
            {
              code: parseInt(res.code),
              total: parseInt(res.data.total),
              artistList: res.data.artistList,
              pageSize: 60,
              getData: that.getSingerList,
            })
          // that.code = parseInt(res.code);
          // that.total = parseInt(res.data.total);
          // that.artistList = res.data.artistList;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    imgError (event) {
      event.currentTarget.src = "https://h5static.kuwo.cn/upload/image/4f768883f75b17a426c95b93692d98bec7d3ee9240f77f5ea68fc63870fdb050.png";
      event.currentTarget.onerror = null;
    }
  },

  computed: {},

  watch: {
    prefix: {
      handler (newVal, oldVal) {
        this.getSingerList(1);
      },
      // immediate: true, //监听首次绑定值
    },
    singerTagId: {
      handler (newVal, oldVal) {
        this.getSingerList(1);
      },
      // immediate: true, //监听首次绑定值
    },
    // artistData: {
    //   handler (newVal, oldVal) {
    //     this.artistData = JSON.parse(JSON.stringify(newVal));
    //   },
    //   immediate: true, //监听首次绑定值
    //   // deep: true,
    // }
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created () {
    this.getSingerList(1);
  },

  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () { }
};
</script>
<style lang="scss" scoped>
li {
  list-style: none;
}
.tag_en,
.tag_kind {
  display: flex;
  flex-wrap: wrap;
  font-size: 14px;
  line-height: 1;
  font-family: "Microsoft YaHei", "Helvetica Neue", Helvetica, STHeiTi,
    sans-serif;
}
.tag_en {
  margin-top: 30px;
  li {
    width: 28px;
    height: 28px;
    margin: 0 3px 6px;
    text-align: center;
    line-height: 28px;
    border-radius: 50%;
    cursor: pointer;
    &:first-child {
      width: 56px;
      border-radius: 14px;
    }
    &.active,
    &:hover {
      background: #31c27c;
      color: #fff;
    }
    &.active {
      font-weight: 600;
    }
  }
}
.tag_kind {
  margin: 20px auto 25px;
  li {
    padding: 7px 14px;
    text-align: center;
    border-radius: 14px;
    cursor: pointer;
    margin: 0 3px 6px;
    &.active,
    &:hover {
      background: #31c27c;
      color: #fff;
    }
    &.active {
      font-weight: 600;
    }
  }
}

.artist_con {
  text-align: center;
  .name {
    position: relative;
    height: 22px;
    font-size: 16px;
    line-height: 22px;
    font-weight: 400;
    margin-top: 10px;
    flex-wrap: nowrap;
    justify-content: center;
    padding: 0 5px;
    .text {
      text-overflow: ellipsis;
      white-space: nowrap;
      cursor: pointer;
      overflow: hidden;
    }
  }
  .num {
    margin-top: 5px;
    font-size: 13px;
    color: #999;
  }
}
.pic_out {
  position: relative;
  width: 100%;
  padding-bottom: 100%;
  overflow: hidden;
  border-radius: 50%;
  img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}

.flex_c {
  display: flex;
  align-items: center;
}
</style>
<style lang="scss">
.artist_con {
  .item {
    width: 13.32% !important;
    margin-left: 10px;
    margin-right: 10px;
    margin-bottom: 30px;
  }
}
</style>



